*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

p{
    margin: 0 0 30px 0;
    color: red;
}

input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus, 
input:-webkit-autofill:active
{
 -webkit-box-shadow: 0 0 0 30px #0e172c inset !important;
}
